import React, { useEffect, useState }from 'react';
import { Image , Descriptions, Button, notification } from 'antd';
import { useParams } from 'react-router-dom';
import AxiosFetch from 'library/hooks/AxiosFetch';
import { useNavigate } from 'react-router-dom';
import { BookingDetailButtons } from '../AccountSettings.style';
import { lockNft } from 'web3/PremierStaking';

const BookingDetail = (props) => {

    const {metamaskConnected, account} = props;

    const navigate = useNavigate();

    let { id } = useParams();
    const [booking, setBooking] = useState({});
    const [collection, setCollection] = useState({});

    useEffect(async () => {
        let url = '/getBookingById';
        const apiData = {
            id: id
        }
        let res = await AxiosFetch(url, apiData, "POST");
        if(res.data.success) {
            setBooking(res.data.booking);
            setCollection(res.data.collection);
        }        
    }, [id]);

    const openNotificationWithIcon = (type, title, description) => {
        notification[type]({
          message: title,
          description: description,
          placement: 'bottomRight'
        });
    };

    const formatDate = (date) => {
        let dateString = '' + date;        
        let newDate = dateString.replace('Z', ' ').replace('T', ' ');        
        return newDate;
    }

    const formatStatus = (status) => {
        switch(status)
        {
            case 'booked':
                return 'Booked';
            case 'locked':
                return 'Locked';
            case 'unlock_available':
                return 'Unlock Available';
            case 'unlock_requested':
                return 'Unlock Requested';
            case 'unlocked':
                return 'Unlocked';
            default:
                return 'Unknown';
        }
    }

    const handleLock = async () => {
        if(!metamaskConnected) {
            openNotificationWithIcon('error', 'Error!', 'You must connect your wallet!');
            return;
        }
        await lockNft(booking.tokenId, true, account).then(
            (data) => {          
              if (data.success == false) {
                if (data.type == "estimategas") {
                  openNotificationWithIcon('error', 'Error!', `Insufficient funds to Lock. Please check your balance and some gas fee!`)              
                } else if (data.type == "lock") {
                  openNotificationWithIcon('error', 'Error!', `Error happened while locking, please try again later`)                            
                }
              }
              else
              {                
                openNotificationWithIcon('success', 'Success!', `NFT Lock Status has been changed.`);
                updateBookingStatus('locked');
              }
            }
        );   
    }

    const handleUnLock = async () => {
        if(!metamaskConnected) {
            openNotificationWithIcon('error', 'Error!', 'You must connect your wallet!');
            return;
        }
        await lockNft(booking.tokenId, false, account).then(
            (data) => {          
              if (data.success == false) {
                if (data.type == "estimategas") {
                  openNotificationWithIcon('error', 'Error!', `Insufficient funds to Unlock. Please check your balance and some gas fee!`)              
                } else if (data.type == "lock") {
                  openNotificationWithIcon('error', 'Error!', `Error happened while unlocking, please try again later`)                            
                }
              }
              else
              {                
                openNotificationWithIcon('success', 'Success!', `NFT Lock Status has been changed.`);
                updateBookingStatus('unlocked');
              }
            }
        );   
    }

    const updateBookingStatus = async (_status) => {
        const body = {
            id: booking.id,
            status: _status
          }
      
        let url = '/updateBookingStatus';
        
        let res = await AxiosFetch(url, body, "POST");
        console.log(res)

        setBooking((prevState) => {
            return({
              ...prevState,
              status: _status
            });
        });
    }
    const formatActionButton = (status) => {
        switch(status)
        {
            case 'booked':
                return (<Button className="dangerButton" type="primary" onClick={handleLock}> Lock </Button>);
            case 'locked':
                return (<Button className="dangerButton" type="primary" onClick={handleUnLock}> Unlock </Button>);
            case 'unlock_available':
                return (<Button className="dangerButton" type="primary" onClick={handleUnLock}> Unlock </Button>);
            case 'unlock_requested':
                return (<Button className="dangerButton" type="primary" onClick={handleUnLock}> Unlock </Button>);
            case 'unlocked':
                return;
            default:
                return;
        }        
    }
    const PhotosArray = (photos) => {
        if(photos === undefined)
        {
            return;
        }            
        const imageArray = JSON.parse(photos);
        return (
            imageArray.map(item => (                
                <Image width={200} src={process.env.REACT_APP_SERVER_URL + "images/" + item} key={item}/>
            )) 
        );
    }
    return (
        <div>
            <BookingDetailButtons>
                <div className="buttons">
                    <Button className="actionButton" type="primary" onClick={() => navigate(-1)}>Back</Button>
                    { formatActionButton(booking.status) }
                </div>
                <span className="statusText">Status: { formatStatus(booking.status) }</span>
            </BookingDetailButtons>
            <Descriptions title="Booking Info" style={{marginTop: "30px"}} bordered>
                <Descriptions.Item label="First Name">{ booking.firstName }</Descriptions.Item>
                <Descriptions.Item label="Last Name">{ booking.lastName }</Descriptions.Item>
                <Descriptions.Item label="Birthday">{ booking.birthday }</Descriptions.Item>

                <Descriptions.Item label="Phone Number">{ booking.phoneNumber }</Descriptions.Item>
                <Descriptions.Item label="Email Address" span={2}>{ booking.email }</Descriptions.Item>
                
                <Descriptions.Item label="Duration">{ booking.startDate } ~ { booking.endDate }</Descriptions.Item>            
                <Descriptions.Item label="Booked Time" span={2}>
                    { formatDate(booking.createdAt) }
                </Descriptions.Item>
                
                <Descriptions.Item label="Lock Days"> { booking.lockDays } </Descriptions.Item>
                <Descriptions.Item label="Location" span={2}>
                    { booking.location }
                </Descriptions.Item>

                <Descriptions.Item label="Owner" span={3}> { booking.account } </Descriptions.Item>                            

                <Descriptions.Item label="Driver License Photos" span={3}>
                    <Image.PreviewGroup>
                        { PhotosArray(booking.driverLicenses) }                    
                    </Image.PreviewGroup>
                </Descriptions.Item>

                <Descriptions.Item label="Insurance Photos" span={3}>
                    <Image.PreviewGroup>
                        { PhotosArray(booking.insurances) }                    
                    </Image.PreviewGroup>
                </Descriptions.Item>            
            </Descriptions>

            <Descriptions title="NFT Info" style={{marginTop: "30px"}} bordered>
                <Descriptions.Item label="Car Name">{ collection.name }</Descriptions.Item>
                {/* <Descriptions.Item label="Vin">{ collection.vin }</Descriptions.Item> */}
                <Descriptions.Item label="Price">{ collection.price }$/Day</Descriptions.Item>

                <Descriptions.Item label={"NFT Image(Token ID: " + booking.tokenId + ")"} span={3}>
                    <Image.PreviewGroup>
                        <Image width={200} src={process.env.REACT_APP_URL + "images/premier/" + booking.tokenId + ".jpg"}/>
                    </Image.PreviewGroup>
                </Descriptions.Item>
            </Descriptions>
        </div>
    );
};

export default BookingDetail;